/* global style start
	============================================================================================ */

body {
  color: $text-color;
  font-family: $text-font;
  font-size: 1.1em;
  font-weight: 400;
  line-height: 1.8;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a:hover {
  text-decoration: none;
}

button {
  cursor: pointer;

  &:focus {
    outline: 0 !important;
    box-shadow: none !important;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $title-font;
  color: $title-color;
  line-height: 1.2;
}

h1,
.h1 {
  font-size: 70px;
  // font-weight: 700;
}
h2,
.h2 {
  font-weight: 600;
  font-size: 26px;

  @media (min-width: 1200px) {
    font-size: 50px;
  }
}
h3,
.h3 {
  font-size: 24px;
  font-weight: 600;
  // font-weight: 500;

  @media (max-width: 1000px) {
    font-size: 19px;
  }
}
h4,
.h4 {
  font-size: 24px;
  font-weight: 600;

  @media (max-width: 450px) {
    font-size: 19px;
  }
}

.primary-text {
  color: $primary-color;
}

.mb-75px {
  margin-bottom: 35px;

  @media (min-width: 992px) {
    margin-bottom: 75px;
  }
}

.bg-lightGray {
  background: #ffffff;
  // background-image: url("../../img/Rectangle.png");
  background-position: center;
  background-size: cover;
}

